.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    top:0;
    left:0;
    width:100%;
    height:99px;
    z-index: 500;
}

.header-bg{
    background-color: rgba(0, 0, 0, 0.85);
    transition: 0.5s;
}

.nav-menu {
    display: flex;
}

.nav-menu li {
    padding: 1rem;
}

.nav-menu li div {
    font-size: 1.2rem;
    font-weight: 500;
    padding:1rem;
}


.nav-menu li div:hover{
    /* padding: 1rem; */
    background-color:rgb(25, 72, 57);
}

.hamburger {
    display: none;
}


@media screen and (max-width:1040px) {
    .nav-menu {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, 0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.3s;
    }


    .nav-menu.active {
        left: 0;
    }


    .nav-menu li {
        padding: 1rem 0;
    }

    .nav-menu li a {
        font-size: 2rem;
    }

    .hamburger {
        display: initial;
    }
}